import { Keystore } from '@xchainjs/xchain-crypto'
import { encode, decode } from 'jwt-simple'
import { Asset } from 'multichain-sdk'
import { ThemeType } from 'theme'

import { ADDRESS } from 'multichain-sdk/config'

const CROSSCHAIN_QUEST_ANNOUNCEMENT = 'CROSSCHAIN_QUEST_ANNOUNCEMENT'

const THEME_TYPE = 'THEME_TYPE'
const CROSSCHAIN_QUEST_MULTICHAIN_KEYSTORE =
  'CROSSCHAIN_QUEST_MULTICHAIN_KEYSTORE'
const CROSSCHAIN_QUEST_MULTICHAIN_ADDR = 'CROSSCHAIN_QUEST_MULTICHAIN_ADDR'
const CROSSCHAIN_QUEST_XDEFI_STATUS = 'CROSSCHAIN_QUEST_XDEFI_STATUS'
const AFFILIATE_ADDRESS = 'AFFILIATE_ADDRESS'

const BASE_CURRENCY = 'BASE_CURRENCY'

export const saveBaseCurrency = (currency: string) => {
  localStorage.setItem(BASE_CURRENCY, currency)
}

export const getBaseCurrency = (): string => {
  return (
    (localStorage.getItem(BASE_CURRENCY) as string) || Asset.USD().toString()
  )
}

export const saveTheme = (themeType: ThemeType) => {
  localStorage.setItem(THEME_TYPE, themeType)
}

export const getTheme = (): ThemeType => {
  return (localStorage.getItem(THEME_TYPE) as ThemeType) || ThemeType.LIGHT
}

export const saveKeystore = (keystore: Keystore) => {
  sessionStorage.setItem(
    CROSSCHAIN_QUEST_MULTICHAIN_KEYSTORE,
    JSON.stringify(keystore),
  )
}

export const getKeystore = (): Keystore | null => {
  const item = sessionStorage.getItem(CROSSCHAIN_QUEST_MULTICHAIN_KEYSTORE)

  if (item) {
    return JSON.parse(item) as Keystore
  }

  return null
}

// save xdefi status to localstorage
export const saveXdefiConnected = (connected: boolean) => {
  if (connected) {
    localStorage.setItem(CROSSCHAIN_QUEST_XDEFI_STATUS, 'connected')
  } else {
    localStorage.removeItem(CROSSCHAIN_QUEST_XDEFI_STATUS)
  }
}

export const getXdefiConnected = (): boolean => {
  return localStorage.getItem(CROSSCHAIN_QUEST_XDEFI_STATUS) === 'connected'
}

export const saveAddress = (address: string) => {
  sessionStorage.setItem(CROSSCHAIN_QUEST_MULTICHAIN_ADDR, address)
}

export const getAddress = (): string | null => {
  const item = sessionStorage.getItem(CROSSCHAIN_QUEST_MULTICHAIN_ADDR)

  if (item) {
    return item
  }
  return null
}

export const setReadStatus = (read: boolean) => {
  localStorage.setItem(CROSSCHAIN_QUEST_ANNOUNCEMENT, read.toString())
}

export const getReadStatus = (): boolean => {
  const read = localStorage.getItem(CROSSCHAIN_QUEST_ANNOUNCEMENT) === 'true'
  return read
}

export const setAffiliates = (affiliates: string[]) => {
  localStorage.setItem(
    AFFILIATE_ADDRESS,
    encode(JSON.stringify(affiliates), ADDRESS as string),
  )
}

export const getAffiliates = (): string[] | null => {
  const item = localStorage.getItem(AFFILIATE_ADDRESS)
  if (item) {
    try {
      return JSON.parse(decode(item, ADDRESS as string)) as string[]
    } catch (e) {
      return null
    }
  }

  return null
}
