import React, { useCallback } from 'react'

import { Asset, Wallet, getAssetBalance } from 'multichain-sdk'

import { FilterList } from 'components/UIElements/FilterList'

import { AssetData } from '../AssetData'

const filterFunction = (asset: Asset, searchTerm: string) => {
  const { ticker } = asset
  return ticker?.toLowerCase().indexOf(searchTerm.toLowerCase()) === 0
}

export type Props = {
  asset: Asset
  assets: Asset[]
  searchDisable: string[]
  withSearch?: boolean
  searchPlaceholder?: string
  onSelect: (value: string) => void
  wallet?: Wallet
  dropdown?: boolean
}

export const AssetMenu: React.FC<Props> = (props): JSX.Element => {
  const {
    searchPlaceholder,
    assets,
    withSearch = true,
    searchDisable = [],
    onSelect = () => {},
    wallet,
    dropdown = false,
  } = props

  const cellRenderer = useCallback(
    (a: Asset) => {
      const balance = wallet && getAssetBalance(a, wallet).amount
      const node = (
        <AssetData dropdown={dropdown} asset={a} amount={balance} full />
      )
      const key = a.toString()
      return { key, node }
    },
    [dropdown, wallet],
  )

  const disableItemFilterHandler = useCallback(
    (a: Asset) => searchDisable.indexOf(a.ticker) > -1,
    [searchDisable],
  )

  return (
    <FilterList
      placeholder={searchPlaceholder}
      searchEnabled={withSearch}
      filterFunction={filterFunction}
      cellRenderer={cellRenderer}
      disableItemFilter={(a: Asset) => disableItemFilterHandler(a)}
      onSelect={onSelect}
      data={assets}
    />
  )
}
