/* eslint-disable react/no-unescaped-entities */
import React, { useCallback } from 'react'

import { Button } from 'components/UIElements/Button'

import { AssetFilterWrapper } from './AssetTypeFilter.style'
import { assetFilterTypes } from './assetTypes'

export type Props = {
  selected: string
  onClick: (key: string) => void
}

export const AssetTypeFilter: React.FC<Props> = (props: Props): JSX.Element => {
  const { selected, onClick } = props

  const handleClick = useCallback(
    (key: string) => {
      onClick(key)
    },
    [onClick],
  )

  return (
    <AssetFilterWrapper className="asset-type-filter">
      {assetFilterTypes.map((data) => (
        <Button
          typevalue="outline"
          round
          onClick={() => handleClick(data.type)}
          focused={selected === data.type}
          fixedWidth={false}
          sizevalue="small"
          key={data.type}
        >
          {data.label}
        </Button>
      ))}
    </AssetFilterWrapper>
  )
}
