import { useCallback } from 'react'

import {
  ConnectedWallet,
  ConnectType,
  useConnectedWallet,
  useWallet as useTerraWallet,
  WalletStatus,
} from '@terra-money/wallet-provider'
import { Keystore } from '@xchainjs/xchain-crypto'
import { Chain, ETHChain } from '@xchainjs/xchain-util'
import { SupportedChain } from 'multichain-sdk'

import { useAppDispatch, useAppSelector } from 'redux/hooks'
import * as walletActions from 'redux/wallet/actions'
import { actions, selectState as selectWalletState } from 'redux/wallet/slice'

import { multichain } from 'services/multichain'

export const useWallet = () => {
  const { connect, status } = useTerraWallet()

  const dispatch = useAppDispatch()
  const walletState = useAppSelector(selectWalletState)

  const { walletLoading, chainWalletLoading } = walletState
  const walletLoadingByChain = Object.keys(chainWalletLoading).map(
    (chain) => chainWalletLoading[chain as SupportedChain],
  )
  const isWalletLoading = walletLoadingByChain.reduce(
    (status, next) => status || next,
    walletLoading,
  )

  const unlockWallet = useCallback(
    async (keystore: Keystore, phrase: string) => {
      // set multichain phrase
      multichain.connectKeystore(phrase)

      dispatch(actions.connectKeystore(keystore))
      dispatch(walletActions.loadAllWallets())
    },
    [dispatch],
  )

  const disconnectWallet = useCallback(() => {
    multichain.resetClients()

    dispatch(actions.disconnect())
  }, [dispatch])

  const connectLedger = useCallback(
    async (chain: Chain) => {
      await multichain.connectLedger({ chain })

      dispatch(walletActions.getWalletByChain(chain as SupportedChain))
    },
    [dispatch],
  )
  const connectedWallet: ConnectedWallet | undefined = useConnectedWallet()

  const connectTerraStationWallet = useCallback(async () => {
    if (status !== WalletStatus.WALLET_CONNECTED) {
      connect(ConnectType.EXTENSION)
    }
    if (connectedWallet) multichain.connectTerraStation(connectedWallet)
    dispatch(walletActions.getWalletByChain(Chain.Terra))
  }, [connect, connectedWallet, dispatch, status])

  const connectXdefiWallet = useCallback(async () => {
    connectTerraStationWallet()
    await multichain.connectXDefiWallet()
    dispatch(walletActions.loadAllWallets())
  }, [connectTerraStationWallet, dispatch])

  const connectMetamask = useCallback(async () => {
    await multichain.connectMetamask()
    dispatch(walletActions.getWalletByChain(ETHChain))
  }, [dispatch])

  const connectTrustWallet = useCallback(async () => {
    await multichain.connectTrustWallet({
      listeners: {
        disconnect: disconnectWallet,
      },
    })

    dispatch(walletActions.getWalletByChain(Chain.Binance))
    dispatch(walletActions.getWalletByChain(Chain.Ethereum))
    dispatch(walletActions.getWalletByChain(Chain.THORChain))
  }, [dispatch, disconnectWallet])

  const setIsConnectWalletModalOpen = useCallback(
    (visible: boolean) => {
      dispatch(actions.setIsConnectWalletModalOpen(visible))
    },
    [dispatch],
  )

  return {
    ...walletState,
    ...walletActions,
    isWalletLoading,
    unlockWallet,
    setIsConnectWalletModalOpen,
    disconnectWallet,
    connectXdefiWallet,
    connectMetamask,
    connectTrustWallet,
    connectTerraStationWallet,
    connectLedger,
  }
}
