import { Coins, MsgSend } from '@terra-money/terra.js'
import { ConnectedWallet } from '@terra-money/wallet-provider'
import {
  TxHash,
  Balance,
  Network,
  TxParams as ClientTxParams,
} from '@xchainjs/xchain-client'
import { Client as TerraClient } from '@xchainjs/xchain-terra'
import {
  assetToString,
  baseAmount,
  Chain,
  TerraChain as TERRAChain,
} from '@xchainjs/xchain-util'
import { XdefiClient } from 'xdefi-sdk'

import { AmountType, Amount, Asset, AssetAmount } from '../entities'
import { IClient } from './client'
import { TxParams, WalletOption } from './types'

const MOCK_PHRASE =
  'image rally need wedding health address purse army antenna leopard sea gain'

export interface ILunaChain extends IClient {
  getClient(): TerraClient
}

export const getTERRAClient = (network: Network, phrase?: string) => {
  return new TerraClient({
    network,
    phrase,
  })
}

export class LunaChain implements ILunaChain {
  private balances: AssetAmount[] = []

  private client: TerraClient

  public readonly chain: Chain

  public walletType: WalletOption | null

  public connectedWallet: ConnectedWallet | null

  constructor({ network = Network.Testnet }: { network?: Network }) {
    this.chain = Chain.Terra
    this.client = getTERRAClient(network, MOCK_PHRASE)
    this.walletType = null
    this.connectedWallet = null
  }

  /**
   * get xchain-terra client
   */
  getClient(): TerraClient {
    return this.client
  }

  get balance() {
    return this.balances
  }

  connectKeystore = (phrase: string) => {
    const network = this.client.getNetwork()
    this.client = getTERRAClient(network, phrase)
    this.walletType = WalletOption.KEYSTORE
  }

  connectTerraStationWallet = async (wallet: ConnectedWallet) => {
    this.client.getAddress = () => wallet.terraAddress as string
    this.connectedWallet = wallet
    const address = this.connectedWallet?.terraAddress as string
    this.client.getAddress = () => address

    const transfer = async (txParams: ClientTxParams) => {
      const { asset } = txParams

      if (!asset) throw Error('invalid asset to transfer')

      const txHash = this.transferUsingConnectedWallet(txParams)

      return txHash
    }

    this.client.transfer = transfer

    this.walletType = WalletOption.TERRASTATION
  }

  connectXdefiWallet = async (xdefiClient: XdefiClient) => {
    if (!xdefiClient) throw Error('xdefi client not found')

    /**
     * 1. load chain provider
     * 2. patch getAddress method
     * 3. patch transfer method
     */
    xdefiClient.loadProvider(TERRAChain)

    if (this.connectedWallet) {
      const address = this.connectedWallet?.terraAddress as string
      this.client.getAddress = () => address
    }

    const transfer = async (txParams: ClientTxParams) => {
      const { asset, amount, recipient, memo } = txParams

      if (!asset) throw Error('invalid asset to transfer')

      const txHash = await xdefiClient.transfer({
        asset: assetToString(asset),
        amount: amount.amount().toNumber(),
        decimal: amount.decimal,
        recipient,
        memo,
      })

      return txHash
    }

    this.client.transfer = transfer

    this.walletType = WalletOption.XDEFI
  }

  disconnect = () => {
    this.client.purgeClient()
    this.walletType = null
  }

  loadBalance = async (): Promise<AssetAmount[]> => {
    try {
      const balances: Balance[] = await this.client.getBalance(
        this.client.getAddress(),
      )

      this.balances = balances.map((data: Balance) => {
        const { asset, amount } = data

        const assetObj = new Asset(asset.chain, asset.symbol)
        const amountObj = new Amount(
          amount.amount(),
          AmountType.BASE_AMOUNT,
          assetObj.decimal,
        )

        return new AssetAmount(assetObj, amountObj)
      })
      return this.balances
    } catch (error) {
      return Promise.reject(error)
    }
  }

  hasAmountInBalance = async (assetAmount: AssetAmount): Promise<boolean> => {
    try {
      await this.loadBalance()

      const assetBalance = this.balances.find((data: AssetAmount) =>
        data.asset.eq(assetAmount.asset),
      )

      if (!assetBalance) return false

      return assetBalance.amount.gte(assetAmount.amount)
    } catch (error) {
      return Promise.reject(error)
    }
  }

  getAssetBalance = async (asset: Asset): Promise<AssetAmount> => {
    try {
      await this.loadBalance()

      const assetBalance = this.balances.find((data: AssetAmount) =>
        data.asset.eq(asset),
      )
      if (!assetBalance)
        return new AssetAmount(asset, Amount.fromAssetAmount(0, asset.decimal))

      return assetBalance
    } catch (error) {
      return Promise.reject(error)
    }
  }

  /**
   * transfer on binance chain
   * @param {TxParams} tx transfer parameter
   */
  transfer = async (tx: TxParams): Promise<TxHash> => {
    // use xchainjs-client standard internally
    try {
      const {
        assetAmount,
        recipient,
        memo,
        // feeOption = FeeOption.Fast,
      } = tx
      const { asset } = assetAmount
      const amount = baseAmount(assetAmount.amount.baseAmount, asset.decimal)
      // eslint-disable-next-line @typescript-eslint/return-await
      return await this.client.transfer({
        asset: asset.getAssetObj(),
        amount,
        recipient,
        memo,
      })
    } catch (error) {
      return Promise.reject(error)
    }
  }

  transferUsingConnectedWallet = async (
    tx: ClientTxParams,
  ): Promise<TxHash> => {
    try {
      const { amount: clientAmount, recipient, memo, asset: clientAsset } = tx

      if (!clientAsset) throw Error()
      const assetObj = new Asset(clientAsset.chain, clientAsset.symbol)
      const amountObj = new Amount(
        clientAmount.amount(),
        AmountType.BASE_AMOUNT,
        assetObj.decimal,
      )

      const assetAmount = new AssetAmount(assetObj, amountObj)
      const { asset } = assetAmount
      const amount = baseAmount(assetAmount.amount.baseAmount, asset.decimal)
      if (!this.client.validateAddress(tx.recipient))
        throw new Error(`${tx.recipient} is not a valid terra address`)

      // TODO use fee?
      // const fee = await this.getFees()

      let amountToSend: Coins.Input = {}

      if (
        asset.chain === Chain.Terra &&
        asset.symbol === 'LUNA' &&
        asset.ticker === 'LUNA'
      ) {
        amountToSend = {
          uluna: `${amount.amount().toFixed()}`,
        }
      } else if (
        asset.chain === Chain.Terra &&
        asset.symbol === 'UST' &&
        asset.ticker === 'UST'
      ) {
        amountToSend = {
          uusd: `${amount.amount().toFixed()}`,
        }
      } else {
        throw new Error(
          'Only LUNA or UST transfers are currently supported on terra',
        )
      }
      const send = new MsgSend(
        this.connectedWallet?.terraAddress as string,
        recipient,
        amountToSend,
      )
      const result = await this.connectedWallet?.post({ msgs: [send], memo })
      return result?.result.txhash as TxHash
    } catch (error) {
      return Promise.reject(error)
    }
  }
}
